*{
    margin: 0;
    box-sizing: border-box;
    font-family: 'Jost', sans-serif;
}
#root{
    min-height: 100vh;
    display: grid;
    place-items: center;
    background: gainsboro;
}
.main{
    min-height: 60vh;
    min-width: 25%;
    background: rgba(140, 200, 220,0.7 );
    border: 10px solid white;
    border-radius: 10px;
}
.create-ani{
    min-height: 30vh;
    width: 100%;
    background: rgb(236, 236, 236);
    border-radius: 50% 50% 0% 0%;
}
@media only screen and (max-width: 600px){
    .main{
        width: 100%;
        height: 100%;
    }
    .create-ani{
        min-height: 50%;
        position: absolute;
        bottom: 1.5rem;
        width: 15rem;
        
    }
}